@keyframes inicio{
	
	0% {opacity: .0;}
	50% {opacity: .8;}
	90% {opacity: .9;}
	100% {opacity: 1;}
	

}

@keyframes kenburns {
    0% {opacity: 0;}
	15% {opacity: 1;}   
    25% {transform: scale(1.05) translate3d(20px, 0, 0);transition: all 2s ease-in;}
    100% {transform: scale(1);}
}