header .menu {
  position: absolute;
  z-index: 999;
  width: 100%;
  top: 20px;
  background-color: #ffffff;
}

header .menu .button-menu-mobile #nav-icon1 {
  width: 60px;
  height: 45px;
  position: relative;
  margin: 50px auto;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .5s ease-in-out;
  -moz-transition: .5s ease-in-out;
  -o-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  cursor: pointer;
  float: right;
}

header .menu .button-menu-mobile #nav-icon1 span, header .menu .button-menu-mobile #nav-icon3 span, header .menu .button-menu-mobile #nav-icon4 span {
  display: block;
  position: absolute;
  height: 9px;
  width: 100%;
  background: #03509e;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .25s ease-in-out;
  -moz-transition: .25s ease-in-out;
  -o-transition: .25s ease-in-out;
  transition: .25s ease-in-out;
}

header .menu .button-menu-mobile #nav-icon1 span:nth-child(1) {
  top: 0px;
}

header .menu .button-menu-mobile #nav-icon1 span:nth-child(2) {
  top: 18px;
}

header .menu .button-menu-mobile #nav-icon1 span:nth-child(3) {
  top: 36px;
}

header .menu .button-menu-mobile #nav-icon1.open span:nth-child(1) {
  top: 18px;
  -webkit-transform: rotate(135deg);
  -moz-transform: rotate(135deg);
  -o-transform: rotate(135deg);
  transform: rotate(135deg);
}

header .menu .button-menu-mobile #nav-icon1.open span:nth-child(2) {
  opacity: 0;
  left: -60px;
}

header .menu .button-menu-mobile #nav-icon1.open span:nth-child(3) {
  top: 18px;
  -webkit-transform: rotate(-135deg);
  -moz-transform: rotate(-135deg);
  -o-transform: rotate(-135deg);
  transform: rotate(-135deg);
}

