@media (max-width: 1280px) {
  .container {
    width: 100%;
  }
  header .menu ul li {
    width: 120px;
  }
}

@media (max-width: 991px) {
  header .button-menu-mobile {
    display: inline-block !important;
    width: 100%;
  }
  header .button-menu-mobile img {
    margin: 40px 0px;
    max-width: 60%;
    float: left;
  }
  header .menu ul {
    height: 1px;
    overflow: hidden;
  }
  header .menu ul li {
    width: 100%;
  }
  header .menu .logo {
    display: none;
  }
  .navbar-header {
    float: none;
  }
  .navbar-left, .navbar-right {
    float: none !important;
  }
  .navbar-toggle {
    display: block;
  }
  .navbar-collapse {
    border-top: 1px solid transparent;
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.1);
  }
  .navbar-fixed-top {
    top: 0;
    border-width: 0 0 1px;
  }
  .navbar-collapse.collapse {
    display: none !important;
  }
  .navbar-nav {
    float: none !important;
    margin-top: 7.5px;
  }
  .navbar-nav > li {
    float: none;
  }
  .navbar-nav > li > a {
    padding-top: 10px;
    padding-bottom: 10px;
  }
  .collapse.in {
    display: block !important;
  }
  .img-mobile {
    width: 100%;
  }
  section.prod .prod-box-img {
    top: 0px;
  }
  section.prod .prod-box-img img {
    border-radius: 20px;
    margin: 0 auto;
  }
  section.prod .prod-status .status {
    text-align: center;
  }
  section.prod .prod-status .prod-buttons {
    text-align: center;
  }
  .text-desc {
    text-align: center;
  }
}


